import { useState, useEffect } from "react";
import configData from "./data/config.json";
import { ProjectConfig } from "./types";
import { Header } from "./components/Header";
import { Footer } from "./components/Footer";
import { TitleSection } from "./components/sections/TitleSection";
import { AbstractSection } from "./components/sections/AbstractSection";
import { ContentSection } from "./components/sections/ContentSection";

function App() {
  const [config, setConfig] = useState<ProjectConfig | null>(null);

  useEffect(() => {
    // In a real app, this could fetch from an API or load async
    // For now we use the imported JSON directly, but casting it to ensure types
    setConfig(configData.project as unknown as ProjectConfig);
  }, []);

  if (!config) return <div className="min-h-screen flex items-center justify-center">Loading...</div>;

  return (
    <div className="min-h-screen bg-white text-slate-900 font-sans selection:bg-slate-200">
      <Header project={config} />
      
      <main className="container mx-auto max-w-5xl px-4 py-12 space-y-24">
        <TitleSection project={config} />
        
        {config.sections.filter(s => s.id === 'tldr').map(section => (
            <ContentSection key={section.id} section={section} />
        ))}

        <AbstractSection abstract={config.abstract} />
        
        <div className="space-y-24">
            {config.sections.filter(s => s.id !== 'tldr').map(section => (
                <ContentSection key={section.id} section={section} />
            ))}
        </div>
      </main>

      <Footer project={config} />
    </div>
  );
}

export default App;
