import { TableData, TableCell } from "../types";

interface TableProps {
  data: TableData;
}

export function Table({ data }: TableProps) {
  const renderCell = (cell: TableCell, index: number, isHeader: boolean = false) => {
    const Component = isHeader ? "th" : "td";
    return (
      <Component
        key={index}
        colSpan={cell.colSpan}
        rowSpan={cell.rowSpan}
        className={`p-2 border border-slate-200 text-sm ${
          isHeader ? "bg-slate-50 font-bold text-slate-800" : "text-slate-600"
        } ${cell.className || ""}`}
      >
        {cell.content}
      </Component>
    );
  };

  return (
    <div className="overflow-x-auto my-6">
      <table className="min-w-full border-collapse border border-slate-200 bg-white shadow-sm rounded-lg overflow-hidden">
        {data.caption && (
          <caption className="p-4 text-sm text-slate-500 italic text-left border-b border-slate-100 bg-slate-50">
            {data.caption}
          </caption>
        )}
        <thead>
          {data.headers.map((row, rowIndex) => (
            <tr key={rowIndex}>
              {row.map((cell, cellIndex) => renderCell(cell, cellIndex, true))}
            </tr>
          ))}
        </thead>
        <tbody>
          {data.rows.map((row, rowIndex) => (
            <tr key={rowIndex} className="hover:bg-slate-50 transition-colors">
              {row.map((cell, cellIndex) => renderCell(cell, cellIndex, false))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
