import ReactMarkdown from "react-markdown";
import { useState } from "react";
import { Section } from "../../types";
import { ImageModal } from "../ImageModal";
import { Table } from "../Table";

export function ContentSection({ section }: { section: Section }) {
  const isCentered = section.style?.includes('text-center');
  const layout = section.layout || "image-bottom"; // Default to bottom if not specified
  const isHorizontal = layout === "image-left" || layout === "image-right";
  const [isModalOpen, setIsModalOpen] = useState(false);
  
  const ImageComponent = () => (
    section.image ? (
      <div className={`flex justify-center ${layout === "image-top" ? "mb-6" : layout === "image-bottom" ? "mt-6" : ""}`}>
        <img 
          src={section.image} 
          alt={section.title} 
          className="max-w-full rounded-lg shadow-sm cursor-zoom-in hover:shadow-md transition-shadow" 
          onClick={() => setIsModalOpen(true)}
        />
      </div>
    ) : null
  );

  return (
    <section className="space-y-4">
      <h2 className={`text-2xl font-bold border-b pb-2 ${isCentered ? 'text-center' : ''} ${section.titleStyle || 'text-slate-900'}`}>{section.title}</h2>
      
      {isHorizontal ? (
        <div className="grid md:grid-cols-2 gap-8 items-start">
          {layout === "image-left" && <ImageComponent />}
          <div className="prose max-w-none text-slate-700 leading-relaxed text-justify">
            <ReactMarkdown>{section.content}</ReactMarkdown>
          </div>
          {layout === "image-right" && <ImageComponent />}
        </div>
      ) : (
        <>
          {layout === "image-top" && <ImageComponent />}
          <div className={`prose max-w-none text-slate-700 leading-relaxed ${isCentered ? 'text-center mx-auto' : 'text-justify'}`}>
            <ReactMarkdown>{section.content}</ReactMarkdown>
          </div>
          {layout === "image-bottom" && <ImageComponent />}
        </>
      )}

      {section.table && <Table data={section.table} />}

      {section.image && (
        <ImageModal 
          src={section.image} 
          alt={section.title} 
          isOpen={isModalOpen} 
          onClose={() => setIsModalOpen(false)} 
        />
      )}
    </section>
  );
}
