import { ProjectConfig } from "../../types";
import { FileText, Github } from "lucide-react";

const icons = {
  FileText,
  Github,
};

export function TitleSection({ project }: { project: ProjectConfig }) {
  return (
    <section className="text-center space-y-6">
      <h1 className="text-4xl md:text-5xl font-bold tracking-tight text-slate-900">
        {project.title}
      </h1>
      
      <div className="space-y-4">
        <div className="flex flex-wrap justify-center gap-x-4 gap-y-2 text-lg text-slate-700">
          {project.authors.map((author, idx) => (
            <span key={idx} className="whitespace-nowrap">
              {author.name}
              <sup className="ml-0.5">{author.affiliation}</sup>
              {author.isEqualContribution && "*"}
              {author.isCorresponding && ""}
              {idx < project.authors.length - 1 && ","}
            </span>
          ))}
        </div>

        <div className="flex flex-wrap justify-center gap-6 text-slate-600">
            {project.affiliations.map((aff, idx) => (
                <span key={idx}>
                    <sup className="mr-1">{idx + 1}</sup>{aff}
                </span>
            ))}
        </div>
        
         <div className="text-sm text-slate-500 flex justify-center gap-4">
            {project.authors.some(a => a.isEqualContribution) && (
                <span>* Equal contribution</span>
            )}
            {project.authors.some(a => a.isCorresponding) && (
                <span> Corresponding author</span>
            )}
        </div>
      </div>

      <div className="flex justify-center gap-4 pt-4">
        {project.links.map((link) => {
          const Icon = icons[link.icon as keyof typeof icons] || FileText;
          return (
            <a
              key={link.name}
              href={link.url}
              className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-slate-900 text-white hover:bg-slate-700 transition-colors font-medium"
            >
              <Icon size={18} />
              {link.name}
            </a>
          );
        })}
      </div>
    </section>
  );
}
